html {
  box-sizing:border-box;
  height: 100%; 
  padding:0;
  margin:0;  
}

div, p {
  padding:0;
  margin:0;
}

html *, html *:before, html *:after {
  box-sizing:inherit;
}

img, video{
  max-width:100%;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  //display: table;
  font-weight: 100;
}

